// const API_URL_AWS = 'http://ec2-3-145-8-106.us-east-2.compute.amazonaws.com'
const API_URL_AWS = ''

export default class APIService {
    static sign_up(body) {
        return fetch(`${API_URL_AWS}/sign_up`, {
            'method': 'POST',
            headers: {
                'content-type': 'application/json'
            },
            body: JSON.stringify(body)
        })
        .then(response => response.json())
        .catch(error => console.log(error))
    }

    static process_template(body) {
        return fetch(`${API_URL_AWS}/process_template`, {
            'method': 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(body)
        })
            .then(response => response.json())
            .catch(error => console.log(error))
    }

    static delete_chemical(body) {
        return fetch(`${API_URL_AWS}/delete_chemical`, {
            'method': 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(body)
        })
            .then(response => response.json())
            .catch(error => console.log(error))
    }

    static generate_heatmap(body) {
        return fetch(`${API_URL_AWS}/generate_heatmap`, {
            'method': 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(body)
        })
            .then(response => response.json())
            .catch(error => console.log(error))
    }


    static GetPlate(body) {
        return fetch(`${API_URL_AWS}/getplate`, {
            'method': 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(body)
        })
            .then(response => response.json())
            .catch(error => console.log(error))
    }

    static PostLogin(body) {
        return fetch(`${API_URL_AWS}/login`, {
            'method': 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(body)
        })
            .then(response => response.json())
            .catch(error => console.log(error))
    }

    static addNewPage(body) {
        return fetch(`${API_URL_AWS}/add_new_page`, {
            'method': 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(body)
        })
            .then(response => response.json())
            .catch(error => console.log(error))
    }

    static switchPage(body) {
        return fetch(`${API_URL_AWS}/switch_page`, {
            'method': 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(body)
        })
            .then(response => response.json())
            .catch(error => console.log(error))
    }

    static updatePageName(body) {
        return fetch(`${API_URL_AWS}/update_page_name`, {
            'method': 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(body)
        })
            .then(response => response.json())
            .catch(error => console.log(error))
    }

    static logout() {
        return fetch(`${API_URL_AWS}/logout`, {
            'method': 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
        })
            .then(response => response.json())
            .catch(error => console.log(error))
    }

    static update_pipette_setup(body) {
        return fetch(`${API_URL_AWS}/update_pipette_setup`, {
            'method': 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(body)
        })
            .then(response => response.json())
            .catch(error => console.log(error))
    }

    static updateDiluentInfo(body) {
        return fetch(`${API_URL_AWS}/update_diluent_info`, {
            'method': 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(body)
        })
            .then(response => response.json())
            .catch(error => console.log(error))
    }

    static updateLoadingInfo(body) {
        return fetch(`${API_URL_AWS}/update_loading_info`, {
            'method': 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(body)
        })
            .then(response => response.json())
            .catch(error => console.log(error))
    }


    static async PostBlocks(blocks) {
        try {
            const response = await fetch(`${API_URL_AWS}/postblocks`, {
                'method': 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(blocks)
            })
            return await response.json()
        } catch (error) {
            return console.log(error)
        }
    }

    static PostPlates(plate) {
        return fetch(`${API_URL_AWS}/postplates`, {
            'method': 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(plate)
        })
        .then(response => response.json())
        .catch(error => console.log(error))
    }

    static updatePlateMetadata(plate) {
        return fetch(`${API_URL_AWS}/update_plate_metadata`, {
            'method': 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(plate)
        })
            .then(response => response.json())
            .catch(error => console.log(error))
    }

    static deletePlate(plate) {
        return fetch(`${API_URL_AWS}/delete_plate`, {
            'method': 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(plate)
        })
            .then(response => response.json())
            .catch(error => console.log(error))
    }

    static PostFile(body, plate_id) {
        return fetch(`${API_URL_AWS}/upload_test/${plate_id}`, {
            'method': 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(body)
        })
            .then(response => response.json())
            .catch(error => console.log(error))
    }

};